@charset "UTF-8";
/**
 * Starry Night Design System
 * Van Gogh-inspired color palette and variables
 * Bold, swirling, celestial aesthetic
 */
:root {
  --starry-night-sky: #172A3A;
  --starry-deep-blue: #004B87;
  --starry-midnight: #0F1A2E;
  --starry-dark-bg: #0A0F1E;
  --starry-golden: #FFD700;
  --starry-golden-glow: #FFC947;
  --starry-golden-bright: #FFEB3B;
  --starry-swirl-blue: #1E5A7D;
  --starry-swirl-cyan: #3A8FB7;
  --starry-swirl-teal: #2C9AA8;
  --starry-horizon: #4A5E8B;
  --starry-cloud: #5B7AA7;
  --starry-village-light: #FFA500;
  --starry-deep-purple: #2D1B4E;
  --starry-violet: #5B3A8F;
  --starry-aurora: #00D9FF;
  --swirl-speed: 20s;
  --particle-speed: 30s;
  --glow-pulse: 3s;
}

