/**
 * @file
 * Starry Night - Core aesthetic styles
 * Van Gogh-inspired swirling, celestial design
 */
body {
  position: relative;
  overflow-x: hidden;
}
body::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(ellipse at 20% 30%, var(--starry-deep-purple) 0%, transparent 50%), radial-gradient(ellipse at 80% 70%, var(--starry-deep-blue) 0%, transparent 50%), radial-gradient(ellipse at 50% 50%, var(--starry-night-sky) 0%, var(--starry-dark-bg) 100%);
  z-index: -2;
  animation: swirling-background var(--swirl-speed) ease-in-out infinite alternate;
}
body::after {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: radial-gradient(circle at 10% 20%, var(--starry-swirl-blue) 0%, transparent 30%), radial-gradient(circle at 90% 80%, var(--starry-violet) 0%, transparent 30%), radial-gradient(circle at 50% 60%, var(--starry-swirl-teal) 0%, transparent 40%);
  opacity: 0.3;
  z-index: -1;
  animation: swirling-overlay calc(var(--swirl-speed) * 1.5) ease-in-out infinite alternate-reverse;
}

@keyframes swirling-background {
  0% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
  50% {
    transform: scale(1.05) rotate(1deg);
    opacity: 0.95;
  }
  100% {
    transform: scale(1) rotate(0deg);
    opacity: 1;
  }
}
@keyframes swirling-overlay {
  0% {
