h1, .h1, h2, .h2, h3, .h3, h4, .h4, h5, .h5, h6, .h6 {
  text-shadow: 0 2px 12px rgba(var(--starry-golden), 0.3), 0 0 24px rgba(var(--starry-golden-glow), 0.2);
  font-weight: 600;
  letter-spacing: -0.02em;
  line-height: 1.2;
}

h1, .h1 {
  background: linear-gradient(135deg, var(--starry-golden-bright), var(--starry-golden-glow));
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
  animation: glow-pulse var(--glow-pulse) ease-in-out infinite;
}

@keyframes glow-pulse {
  0%, 100% {
    filter: brightness(1) drop-shadow(0 0 8px rgba(255, 215, 0, 0.3));
  }
  50% {
    filter: brightness(1.2) drop-shadow(0 0 16px rgba(255, 215, 0, 0.5));
  }
}
p, li, span {
  line-height: 1.7;
  letter-spacing: 0.01em;
}

a {
  position: relative;
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
a::after {
  content: "";
  position: absolute;
  bottom: -2px;
  left: 0;
  width: 0;
  height: 2px;
  background: linear-gradient(90deg, var(--starry-golden), var(--starry-golden-bright));
  transition: width 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}
a:hover {
  color: var(--starry-golden-bright);
  text-shadow: 0 0 12px rgba(255, 215, 0, 0.4);
}
a:hover::after {
  width: 100%;
}

.region,
.block,
.layout-container {
  position: relative;
  z-index: 1;
}

.page-wrapper {
  position: relative;
  z-index: 1;
  padding-top: 2rem;
  padding-bottom: 4rem;
}

.card,
.view-content,
.block-content {
  background: rgba(var(--starry-night-sky), 0.6);
  backdrop-filter: blur(12px);
  border: 1px solid rgba(var(--starry-swirl-cyan), 0.2);
  border-radius: var(--bs-border-radius-lg, 20px);
  box-shadow: 0 8px 32px rgba(0, 0, 0, 0.3), inset 0 1px 0 rgba(255, 255, 255, 0.1);
  transition: all 0.4s cubic-bezier(0.4, 0, 0.2, 1);
}
.card:hover,
.view-content:hover,
.block-content:hover {
  transform: translateY(-4px);
  box-shadow: 0 16px 48px rgba(0, 0, 0, 0.4), 0 0 24px rgba(var(--starry-golden), 0.15), inset 0 1px 0 rgba(255, 255, 255, 0.15);
  border-color: rgba(var(--starry-golden-glow), 0.3);
}

.navbar {
  background: linear-gradient(180deg, rgba(var(--starry-midnight), 0.95) 0%, rgba(var(--starry-night-sky), 0.85) 100%);
  backdrop-filter: blur(16px);
  border-bottom: 1px solid rgba(var(--starry-golden), 0.2);
  box-shadow: 0 4px 24px rgba(0, 0, 0, 0.3);
}
.navbar .nav-link {
  position: relative;
  transition: all 0.3s ease;
}
.navbar .nav-link::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 50%;
  width: 0;
  height: 2px;
  background: var(--starry-golden);
  transform: translateX(-50%);
  transition: width 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.navbar .nav-link:hover, .navbar .nav-link:focus {
  color: var(--starry-golden-bright);
  text-shadow: 0 0 12px rgba(255, 215, 0, 0.4);
}
.navbar .nav-link:hover::before, .navbar .nav-link:focus::before {
  width: 80%;
}
.navbar .nav-link.active {
  color: var(--starry-golden);
}
.navbar .nav-link.active::before {
  width: 100%;
}

.navbar-brand {
  font-family: var(--bs-font-serif);
  font-size: clamp(1.25rem, 3vw, 1.75rem);
  font-weight: 700;
  letter-spacing: -0.02em;
  text-shadow: 0 2px 12px rgba(255, 215, 0, 0.3);
}

.btn {
  position: relative;
  overflow: hidden;
  border-radius: var(--bs-border-radius);
  transition: all 0.3s cubic-bezier(0.4, 0, 0.2, 1);
}
.btn::before {
  content: "";
  position: absolute;
  top: 50%;
  left: 50%;
  width: 0;
  height: 0;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.2);
  transform: translate(-50%, -50%);
  transition: width 0.6s, height 0.6s;
}
.btn:hover::before {
  width: 300px;
  height: 300px;
}
.btn:hover {
  transform: translateY(-2px);
  box-shadow: 0 8px 24px rgba(var(--starry-golden), 0.3);
}

.btn-primary {
  background: linear-gradient(135deg, var(--starry-golden), var(--starry-golden-glow));
  border: none;
  color: var(--starry-midnight);
  font-weight: 600;
}
.btn-primary:hover, .btn-primary:focus {
  background: linear-gradient(135deg, var(--starry-golden-bright), var(--starry-golden));
  color: var(--starry-dark-bg);
  box-shadow: 0 8px 32px rgba(255, 215, 0, 0.4);
}

.btn-secondary {
  background: rgba(var(--starry-swirl-cyan), 0.8);
  border: 1px solid rgba(var(--starry-swirl-cyan), 1);
}
.btn-secondary:hover, .btn-secondary:focus {
  background: rgba(var(--starry-aurora), 0.9);
  border-color: var(--starry-aurora);
  transform: translateY(-2px);
}

.footer {
  position: relative;
  margin-top: 4rem;
  padding: 3rem 0 2rem;
  background: linear-gradient(180deg, transparent 0%, rgba(var(--starry-midnight), 0.8) 20%, rgba(var(--starry-dark-bg), 0.95) 100%);
  border-top: 1px solid rgba(var(--starry-golden), 0.2);
}
.footer::before {
  content: "";
  position: absolute;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 4px;
  background: linear-gradient(90deg, transparent 0%, var(--starry-village-light) 20%, var(--starry-golden) 50%, var(--starry-village-light) 80%, transparent 100%);
  animation: village-lights 4s ease-in-out infinite;
}

@keyframes village-lights {
  0%, 100% {
    opacity: 0.5;
  }
  50% {
    opacity: 1;
  }
}
.fade-in-up {
  opacity: 0;
  transform: translateY(30px);
  animation: fadeInUp 0.8s ease forwards;
}

@keyframes fadeInUp {
  to {
    opacity: 1;
    transform: translateY(0);
  }
}
.fade-in-up:nth-child(1) {
  animation-delay: 0.1s;
}

.fade-in-up:nth-child(2) {
  animation-delay: 0.2s;
}

.fade-in-up:nth-child(3) {
  animation-delay: 0.3s;
}

.fade-in-up:nth-child(4) {
  animation-delay: 0.4s;
}

.fade-in-up:nth-child(5) {
  animation-delay: 0.5s;
}

.fade-in-up:nth-child(6) {
  animation-delay: 0.6s;
}

.fade-in-up:nth-child(7) {
  animation-delay: 0.7s;
}

.fade-in-up:nth-child(8) {
  animation-delay: 0.8s;
}

.fade-in-up:nth-child(9) {
  animation-delay: 0.9s;
}

.fade-in-up:nth-child(10) {
  animation-delay: 1s;
}

@media (prefers-reduced-motion: reduce) {
  *,
  *::before,
  *::after {
    animation-duration: 0.01ms !important;
    animation-iteration-count: 1 !important;
    transition-duration: 0.01ms !important;
  }
}
@media (prefers-contrast: high) {
  body::before,
  body::after {
    opacity: 0.5;
  }
  .star {
    background: #fff;
    box-shadow: 0 0 2px #fff;
  }
  a,
  .btn {
    border: 2px solid currentColor;
  }
}
.loading-swirl {
  display: inline-block;
  width: 48px;
  height: 48px;
  border: 4px solid rgba(var(--starry-golden), 0.2);
  border-top-color: var(--starry-golden);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}
@media (max-width: 767.98px) {
  body::before,
  body::after {
    animation-duration: calc(var(--swirl-speed) * 2);
  }
  .starry-container {
    display: none;
  }
  h1, .h1 {
    font-size: clamp(2rem, 8vw, 3rem);
  }
}
@media (min-width: 768px) and (max-width: 1199.98px) {
  .page-wrapper {
    padding-top: 3rem;
    padding-bottom: 5rem;
  }
}
@media (min-width: 1200px) {
  .card:hover,
  .view-content:hover,
  .block-content:hover {
    transform: translateY(-8px) scale(1.01);
  }
}
/**
